@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,p,a{margin:0}input[type=number]{-moz-appearance:textfield}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.app{align-items:center;justify-content:center;font-family:"Montserrat",sans-serif;margin:40px 40px}.outputContainer{flex:1 1}.outputContainer .title{font-size:26px;margin:0 0 20px 0}.outputContainer .body .text{font-size:18px;margin:0 0 20px 0}.inputContainer{flex:1 1;background:#222;background:linear-gradient(to top, #222222, #000000);color:#fff;text-align:center;border-radius:10px;padding:40px 0}.inputContainer .title{font-size:18px;margin:0 0 30px 0}.inputContainer .buttonContainer{margin:0 0 30px 0}.inputContainer .buttonContainer a{background:#fff;color:#000;border-radius:4px;padding:8px 15px;height:10px;margin:0 10px}.inputContainer .heightWeightContainer{display:flex;align-items:center;justify-content:center;margin:0 0 30px 0}.inputContainer .heightWeightContainer .height{margin:0 10px}.inputContainer .heightWeightContainer .height input{background:#222;color:#fff;font-size:22px;text-align:center;border:none;width:50%;padding:10px;border-radius:4px}.inputContainer .heightWeightContainer .height .text{margin:0 0 10px 0}.inputContainer .heightWeightContainer .weight{margin:0 10px}.inputContainer .heightWeightContainer .weight input{background:#222;color:#fff;font-size:22px;text-align:center;border:none;width:50%;padding:10px;border-radius:4px}.inputContainer .heightWeightContainer .weight .text{margin:0 0 10px 0}.inputContainer .bmiContainer{margin:0 0 30px 0}.inputContainer .bmiContainer .text{margin:0 0 10px 0}.inputContainer .bmiContainer .bmi{font-size:36px}.inputContainer .text{color:#dcdcdc}.inputContainer .chart{background:#222;background:linear-gradient(90deg, #3e8ee3 0%, #7cc9cf 25%, #92db71 50%, #fadb74 75%, #dc584c 100%);height:30px;width:80%;margin:0 auto;border-radius:4px}.inputContainer .chart .arrow{width:4px;height:inherit;background:#000}@media screen and (min-width: 768px){.app{display:flex;align-items:center;justify-content:center;height:100vh;max-width:1024px;margin:0 auto}.app .outputContainer{margin:0 20px 0 40px}.app .outputContainer .title{font-size:52px}.app .outputContainer .body .text{font-size:20px}.app .inputContainer{margin:0 40px 0 20px}}
